import React from 'react'
import { Card, Row, Col } from 'react-bootstrap'
import juegoprot from '../assets/images/juegoprot.jpg';

export function Juegos () {
    return (
        <div className="contjuegos">
            <Row xs={1} md={3} className="g-4">
                {Array.from({ length: 9 }).map((_, idx) => (
                    <Col>
                    <Card className="caraJuego">
                        <Card.Img variant="top" src={juegoprot} />
                        <Card.Body>
                        <Card.Title>Nombre del Juego</Card.Title>
                        <Card.Text>
                            Esta es la descripcion principal del juego para que el usuario pueda hacerse una idea y si es de su interes mirar mas detalladamente el juego.
                        </Card.Text>
                        </Card.Body>
                    </Card>
                    </Col>
                ))}
            </Row>
        </div>
    );
}